import {
  Anchor,
  Button,
  Divider,
  Input,
  Layout,
  Radio,
  Select,
  Space,
  Typography,
} from "antd";
import { AnchorContainer } from "antd/es/anchor/Anchor";
import IconFont from "../components/iconfont";

const { TextArea } = Input;
const { Title, Text } = Typography;
const { Sider, Content } = Layout;

export default function Setting() {
  return (
    <div style={{ display: "flex", flexDirection: "column", height: "100%" }}>
      <Title level={3}></Title>
      <Layout
        id="setting-container"
        style={{
          overflow: "auto",
          flex: 1,
          backgroundColor: "#ffffff",
        }}
      >
        <Sider
          theme="light"
          style={{ position: "sticky", overflowY: "auto", top: 0 }}
        >
          <div style={{ marginTop: 20 }}>
            <Anchor
              onClick={(ev) => ev.preventDefault()}
              getContainer={() =>
                document.getElementById("setting-container") as AnchorContainer
              }
              affix={false}
              items={[
                {
                  key: "account",
                  href: "#setting-account",
                  title: (
                    <Space>
                      <IconFont type="icon-account" />
                      <Text></Text>
                    </Space>
                  ),
                },
                {
                  key: "apperance",
                  href: "#setting-appearance",
                  title: (
                    <Space>
                      <IconFont
                        type="icon-appearance"
                        style={{ fontSize: 15 }}
                      />
                      <Text></Text>
                    </Space>
                  ),
                },
                {
                  key: "language",
                  href: "#setting-language",
                  title: (
                    <Space>
                      <IconFont type="icon-language" style={{ fontSize: 15 }} />
                      <Text></Text>
                    </Space>
                  ),
                },
                {
                  key: "backup",
                  href: "#setting-backup",
                  title: (
                    <Space>
                      <IconFont type="icon-backup" style={{ fontSize: 15 }} />
                      <Text></Text>
                    </Space>
                  ),
                },
                {
                  key: "advice",
                  href: "#setting-advice",
                  title: (
                    <Space>
                      <IconFont type="icon-advice" style={{ fontSize: 15 }} />
                      <Text></Text>
                    </Space>
                  ),
                },
                {
                  key: "aboutus",
                  href: "#setting-aboutus",
                  title: (
                    <Space>
                      <IconFont type="icon-aboutus" style={{ fontSize: 15 }} />
                      <Text></Text>
                    </Space>
                  ),
                },
              ]}
            />
          </div>
        </Sider>
        <Content style={{ marginLeft: 15 }}>
          <Title level={5} style={{ marginTop: 0 }} id="setting-account">
            
          </Title>
          <div style={{ marginTop: 15 }}>
            <Space direction="vertical" size={15}>
              <Space>
                <Button type="primary"></Button>
                <Text style={{ color: "#000000a6" }}></Text>
                <Text style={{ color: "#000000a6" }} underline>
                  
                </Text>
              </Space>
            </Space>
          </div>
          <Divider />
          <Title level={5} id="setting-appearance">
            
          </Title>
          <div style={{ marginTop: 15 }}>
            <Space direction="vertical" size={15}>
              <Radio.Group defaultValue="light" buttonStyle="solid">
                <Radio.Button value="light"></Radio.Button>
                <Radio.Button value="dark"></Radio.Button>
                <Radio.Button value="system"></Radio.Button>
              </Radio.Group>
            </Space>
          </div>
          <Divider />
          <Title level={5} id="setting-language">
            
          </Title>
          <div style={{ marginTop: 15 }}>
            <Space direction="vertical" size={15}>
              <Select
                defaultValue="zh-CN"
                style={{ width: 120 }}
                options={[
                  { value: "zh-CN", label: "" },
                  { value: "en-US", label: "English" },
                ]}
              />
            </Space>
          </div>
          <Divider />
          <Title level={5} id="setting-backup">
            
          </Title>
          <div style={{ marginTop: 15 }}>
            <Space direction="vertical" size={15}>
              <Space>
                <Button type="primary"></Button>
                <Text style={{ color: "#000000a6" }}>
                  2023.08.05 12:23:11
                </Text>
              </Space>
            </Space>
          </div>
          <Divider />
          <Title level={5} id="setting-advice">
            
          </Title>
          <div style={{ marginTop: 15 }}>
            <Space direction="vertical" size={15}>
              <TextArea
                rows={4}
                cols={60}
                showCount
                style={{ height: 120, resize: "none" }}
                placeholder="......"
              />
              <Button type="primary" disabled>
                
              </Button>
            </Space>
          </div>
          <Divider />
          <Title level={5} id="setting-aboutus">
            
          </Title>
          <div style={{ marginTop: 15 }}>
            <Space direction="vertical" size={15}>
              <Text style={{ color: "#000000a6" }}>
                 .NET/C# 
              </Text>
            </Space>
          </div>
          <Divider />
        </Content>
      </Layout>
    </div>
  );
}
